<template>
    <v-container fluid :class="user && user.isAdmin ? '' : 'fill-height'">
        <template v-if="user && user.isAdmin">
          <v-row dense>
            <v-col>
                <h2 class="title">Dashboard</h2>
            </v-col>
          </v-row>
          <v-row v-if="loading">
              <v-col v-for="i in 6" cols="4" :key="i">
                  <v-skeleton-loader type="card"></v-skeleton-loader>
              </v-col>
          </v-row>
          <v-row dense v-else>
            <v-col v-for="exam in exams" :key="exam.title">
                <v-card outlined>
                    <v-list-item three-line dense>
                        <v-list-item-content>
                            <div class="overline mb-4 primary--text">{{exam.time}} minutes</div>
                            <v-list-item-title class="headline mb-1">{{exam.title}}</v-list-item-title>
                            <v-list-item-subtitle>{{exam.date}}</v-list-item-subtitle>
                        </v-list-item-content>
                        <v-list-item-icon>
                            <v-btn icon>
                                <v-tooltip bottom>
                                    <template v-slot:activator="{ on }">
                                        <v-icon color="success" v-if="exam.finish" v-on="on">mdi-check</v-icon>
                                        <v-icon color="warning" v-else v-on="on">mdi-information</v-icon>
                                    </template>
                                    <span v-if="exam.finish">Exam has been Finished</span>
                                    <span v-else-if="!exam.start && !exam.finish">Exam has not been Finished</span>
                                    <span v-else>Exam has not been Started</span>
                                </v-tooltip>
                            </v-btn>
                        </v-list-item-icon>
                    </v-list-item>
                    <v-card-actions>
                        <v-btn depressed color="success" class="mt-2" v-if="exam.finish">Finished</v-btn>
                        <v-btn depressed color="primary" class="mt-2" :to="`/exam/`+exam.title" :disabled="exam.start" v-else>Start Exam</v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
          </v-row>
        </template>
        <template v-else>
          <v-row dense justify="center" align="center" v-if="detail">
              <v-col class="text-center" sm="12" md="3">
                  <v-card outlined class="pa-5">
                      <v-card-text>
                        <v-avatar color="grey" size="200">
                            <v-img src=""/>
                        </v-avatar>
                        <div class="my-5">
                            <h2 class="title">{{detail.name}}</h2>
                            <span class="subtitle-1">NISN : {{detail.username}}</span>
                            <br>
                            <span class="subtitle-1">Jenis Kelamin : {{detail.metas.gender}}</span>
                        </div>
                        <v-btn depressed color="success" block to="/sequence" v-if="sequences">Lanjutkan</v-btn>
                        <v-btn depressed color="primary" block to="/start" v-else>Start Exam</v-btn>
                        <v-btn text @click="setLogout" block>Logout</v-btn>
                      </v-card-text>
                  </v-card>
              </v-col>
          </v-row>
        </template>
    </v-container>
</template>

<script>
import {mapState,mapMutations,mapActions} from 'vuex'

export default {
  name: 'Dashboard',
  data(){
      return{
          exams:[
              {title:'Mathematics Exam',time:'90',date:'Friday, 15 May 2020',start:false,finish:true},
              {title:'Bahasa Exam',time:'90',date:'Saturday, 16 May 2020',start:false,finish:false},
              {title:'English Exam',time:'90',date:'Sunday, 17 May 2020',start:false,finish:false},
          ],
      }
  },
  created(){
      this.checkDate()
      this.userDetail()
  },
  computed:{
    ...mapState({
        loading: state => state.loading,
        user : state => state.userLoggedIn,
        detail : state => state.auth.detail,
        sequences : state => state.participant.sequences
    }),
  },
  methods:{
    ...mapMutations(['setLoading']),
    ...mapActions(['setLogout','userDetail']),
    checkDate(){
          const now = new Date()

          this.exams.map(exam=>{
              const date = new Date(exam.date)
              const tomorrow = new Date()
              tomorrow.setDate(date.getDate()+1)
              
              if(date > tomorrow || now > date){
                exam.start = false  
              }else if(now < date){
                exam.start = true
              }

          })
    },
  }
}
</script>
