<template>
    <v-container>
        <v-row justify="center">
            <v-col sm="12" md="6">
                <v-row dense>
                    <v-col class="text-center">
                        <v-chip color="primary">{{data.countdown}} Detik</v-chip>
                    </v-col>
                </v-row>
                <v-row dense>
                    <v-col cols="12" v-for="content in data.contents" :key="content._id">
                        <v-card outlined>
                            <v-card-title>{{content.parent.title}}</v-card-title>
                            <v-card-text>
                                <p class="subtitle" v-html="content.parent.description"></p>
                                <v-radio-group @change="selectedAnswer" dense v-model="radioGroup">
                                    <v-radio v-for="answer in content.childs" :key="answer._id" :value="answer._id">
                                        <template v-slot:label>
                                            <span v-html="answer.description"></span>
                                        </template>
                                    </v-radio>
                                </v-radio-group>
                            </v-card-text>
                        </v-card>
                    </v-col>
                </v-row>
                <v-row dense>
                    <v-col class="text-right">
                        <v-btn @click="next" depressed color="primary" block>
                            {{index == sequences.length-1 ? 'Selesai' : 'Selanjutnya'}} <v-icon>{{index == sequences.length-1 ? 'mdi-check' :'mdi-arrow-right'}}</v-icon>
                        </v-btn>
                    </v-col>
                </v-row>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { mapState } from 'vuex'
    export default {
        name:'Sequence',
        data(){
            return{
                answer:undefined,
                data:null,
                index:0,
                interval:null,
                radioGroup:null
            }
        },
        created(){
            this.data = this.sequences[this.index]
            this.countdown()
        },
        computed:{
            ...mapState({
                sequences:state=>state.participant.sequences,
            }),
        },
        methods:{
            selectedAnswer(e){
                localStorage.setItem(this.data._id,e)
            },
            countdown(){
                this.interval = setInterval(()=>{                    
                    if(this.data.countdown < 1){
                        this.next()
                    }else{
                        this.data.countdown -= 1
                    }
                },1000)
            },
            next(){
                if(this.index == this.sequences.length-1){
                    this.finish()
                }else{
                    this.index += 1
                    this.data = this.sequences[this.index]
                }
            },
            finish(){ 
                clearInterval(this.interval)
                this.$router.push({name:'finish'})
            },
            getAnswerChecked(){
                const answered = localStorage.getItem(this.data._id)
                this.radioGroup = answered
            }
        },
        watch:{
            data(){
                this.getAnswerChecked()
            }   
        },
    }
</script>

<style lang="scss" scoped>

</style>