import axios from 'axios'
import qs from 'querystring'
import router from '../router'

const state = {
    res:null,
    sequences: JSON.parse(localStorage.getItem("sequences"))
}

const getters = {}

const mutations = {}

const actions = {
    startExam({rootState},data){
        rootState.loading = true
        axios
            .post(process.env.VUE_APP_URL+'participant/exam/start',qs.stringify(data),{
                headers:{
                    'Authorization':rootState.userLoggedIn.token,
                    'Content-Type':'application/x-www-form-urlencoded'
                }
            })
            .then(res=>{
                localStorage.setItem("sequences",JSON.stringify(res.data.data))
                rootState.loading = false
                router.push({name:'sequence'})
                location.reload()
            })
    },
}

export default{
    state,getters,mutations,actions,namespaced : true
}