<template>
    <v-container fluid class="fill-height">
        <v-row dense align="center" justify="center">
            <v-col sm="12" md="3" class="text-center">
                <h2 class="headline my-5">Fill Some Information</h2>
                <v-form ref="form" @submit.prevent="start" id="form-start">
                    <v-text-field dense outlined v-model="cita_cita" :loading="loading" :rules="rules" label="Cita - Cita" />
                    <v-text-field dense outlined v-model="hoby" :loading="loading" :rules="rules" label="Hoby" />
                    <v-text-field dense outlined v-model="jurusan" :loading="loading" :rules="rules" label="Jurusan" />
                    <v-btn depressed class="primary" type="submit" form="form-start" :loading="loading" block>Submit</v-btn>
                </v-form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    
    export default {
        name:'Start',
        data(){
            return{
                cita_cita:'',
                hoby:'',
                jurusan:'',
                rules : [
                    v => !!v || 'This field is required'
                ],
            }
        },
        created(){
            this.userDetail()
        },
        computed:{
            ...mapState({
                loading : state => state.loading,
                user: state => state.userLoggedIn,
                detail : state => state.auth.detail
            })
        },
        methods:{
            ...mapActions('participant',['startExam']),
            ...mapActions(['userDetail']),
            start(){
                if(this.$refs.form.validate() && this.detail){
                    this.startExam({exam_id:this.detail.metas.exam_id,cita_cita:this.cita_cita,hoby:this.hoby,jurusan:this.jurusan})
                }
            }
        }
    }
</script>

